/* CSS */
.button-8 {
    background-color: transparent;
    border-radius: 0px;
    border: 1px solid black;
    /*box-shadow: rgba(255, 255, 255, .7) 0 1px 0 0 inset;*/
    box-sizing: border-box;
    color: black;
    cursor: pointer;
    display: inline-block;
    font-family: -apple-system, system-ui, "Segoe UI", "Liberation Sans", sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 1.15385;
    margin: 0;
    outline: none;
    padding: 8px .8em;
    position: relative;
    text-align: center;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    vertical-align: baseline;
    white-space: nowrap;
}

.button-8:hover,
.button-8:focus {
    /*background-color: rgb(141, 224, 153);*/
    /*background-color:#ff8265;*/
    background-color: #91caff;
    color: #000000;
}

.button-8:focus {
    box-shadow: 0 0 0 4px rgba(0, 149, 255, .15);
}

.button-8:active {
    background:transparent;
    box-shadow: none;
    color:#000000;
}


.side-buttons{
    position:relative;
    top:5px;
    right:15px;
    float:right;
    margin-top:15px;
    margin-right:13%;
}